.mianhe {
    margin: 20px 0;
    width: 100%;
    height: 140px;
    font-family: sans-serif;
    border: 1px solid transparent;
}

.mianhe-inner {
    margin: 40px auto;
    max-width: 780px;
    height: 60px;
    line-height: 54px;
    border: 3px solid #4528FF;
    background-color: #0060FF;
}

.choose-city {
    position: relative;
    width: 15%;
    text-indent: 2em;
    background-color: #E3E4FF;
}

.choose-city>span {
    cursor: pointer;
}

.choose-city>span i {
    margin-left: 20px;
}

.rota {
    -webkit-transform: rotate(180deg);
    -ms-transform: rotate(180deg);
    transform: rotate(180deg);
}

.choose-city ul {
    position: absolute;
    top: 80px;
    left: 20px;
    z-index: 1;
    display: none;
    background-color: #fff;
    border-radius: 6px;
}

#sjx {
    position: absolute;
    top: -20px;
    left: 10px;
    width: 0;
    height: 0;
    border: 10px solid #dedede;
    border-left-color: transparent;
    border-right-color: transparent;
    border-top-color: transparent;
}

.choose-city ul li {
    width: 116px;
    line-height: 40px;
    text-indent: 1.5em;
    color: #666;
    font-size: 16px;
    cursor: pointer;
}

.choose-city ul li:hover {
    background-color: #dedede;
    border-radius: 6px;
}

.company {
    width: 59%;
    color: #444;
    background-color: #fff;
}

.company input {
    display: block;
    padding-left: 20px;
    width: 100%;
    font-size: 17px;
    border: none;
}

.chaxun {
    width: 26%;
    line-height: 54px;
    color: #fff;
    text-align: center;
    font-size: 18px;
    font-weight: bold;
    cursor: pointer;
}

@media screen and (max-width: 768px) {
    .choose-city {
        text-indent: .5em;
        width: 20%;
    }
    .choose-city>span {
        white-space: nowrap;
    }
    .choose-city>span i {
        margin-left: 4px;
    }
    .company {
        width: 55%;
    }
    .company input {
        padding-left: 4px;
        font-size: 12px;
    }
    .chaxun {
        width: 25%;
        font-size: 12px;
    }
}

/* getphone */

.getphone {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, .65);
    z-index: 9999999;
}

.getphone-inner {
    position: relative;
    left: calc(50% - 250px);
    top: 15%;
    padding: 40px;
    width: 580px;
    height: auto;
    border-radius: 8px;
    background-color: #fff;
    overflow: hidden;
}

.getphone-inner .shut {
    position: absolute;
    top: 15px;
    right: 15px;
    width: 20px;
    height: 20px;
    background-image: url('../images/nav-btn2.png');
    background-repeat: no-repeat;
    background-size: cover;
    cursor: pointer;
}

.getphone h3 {
    margin: 0;
    width: 100%;
    font-size: 28px;
    color: #F04134;
    text-align: center;
}

.getphone p {
    margin: 0;
    margin-top: 10px;
    color: #8f8f8f;
    text-align: center;
}

.getphone form {
    margin-top: 40px;
    width: 100%;
}

.getphone form p {
    margin-top: 10px;
}

.getphone form p label {
    width: 100px;
    color: #666;
    font-weight: 100;
    line-height: 40px;
    font-size: 16px;
    text-align-last: right;
}

.getphone form p input {
    padding-left: 10px;
    width: calc(100% - 110px);
    line-height: 40px;
    border-radius: 4px;
}

.getphone form div {
    margin-top: 20px;
    text-align: right;
}

.getphone form div input {
    padding: 10px 16px;
    color: #fff;
    background-color: #F04134;
    border-radius: 4px;
}

.fankuiye>p {
    margin-bottom: 60px;
}

.guanzhu,
.qukankan {
    width: 45%;
}

.guanzhu img {
    display: block;
    margin: 0 auto;
}

.fengexian {
    width: 5%;
}

.fengexian p {
    margin: 0 auto;
    width: 1px;
    height: 130px;
    background-color: #999;
}

.qukankan {
    text-align: center;
}

.qukankan h4 {
    margin: 0;
    margin-bottom: 6px;
}

.qukankan li {
    margin: 0 auto;
    width: 40%;
}

.qukankan ul li a {
    color: #04A3E5;
    display: block;
    line-height: 1.7em;
    text-decoration: none;
}

.qukankan li a:hover {
    color: #fff;
    background-color: #04A3E5;
}

@media screen and (max-width: 768px) {
    .getphone-inner {
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        border-radius: 0;
    }
    .qukankan ul li a {
        white-space: nowrap;
    }
}

/* getphone end */


