/* motai */

.motai {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, .65);
    z-index: 9999;
}

.mo-inner {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    background-color: #fff;
    width: 520px;
    height: 400px;
    border-radius: 4px;
}

.mo-inner h2 {
    margin: 0;
    padding: 0 12px;
    line-height: 40px;
    border-bottom: 1px solid #ccc;
    font-size: 16px;
}

.mo-inner h3 {
    margin-top: 80px;
    text-align: center;
    font-size: 28px;
    font-family: '黑体';
    letter-spacing: 1px;
}

.mo-inner p {
    margin-top: 30px;
    text-align: center;
    font-size: 32px;
}

.mo-inner a {
    margin: 30px auto;
    display: block;
    text-align: center;
    width: 30%;
    line-height: 40px;
    background-color: #F04134;
    color: #fff;
    border-radius: 6px;
}

/* motai end */


