/* fix phone */

.fix-phone {
    position: fixed;
    left: 10px;
    bottom: calc(30% - 60px);
    padding: 8px 12px;
    width: 280px;
    font-size: 24px;
    border-radius: 8px;
    z-index: 999999;
    background: -webkit-gradient(linear, left top, right bottom, from(#1446EE), color-stop(#04A3E5), to(#1446EE));
    background: -webkit-linear-gradient(left top, #1446EE, #04A3E5, #1446EE);
    background: -o-linear-gradient(left top, #1446EE, #04A3E5, #1446EE);
    background: linear-gradient(to right bottom, #1446EE, #04A3E5, #1446EE);
    text-align: center;
}

.fix-phone a {
    display: block;
    color: #fff;
    text-decoration: none;
}

.fix-phone .fa-phone-square {
    padding: 12px 24px;
    line-height: 40px;
}

@media screen and (max-width: 768px) {
    .fix-phone {
        left: 0;
        bottom: 0;
        width: 100%;
    }
    .fix-phone .fa-phone-square {
        padding: 0;
        line-height: 74px;
        font-size: 20px;
    }
}

/* fix phone end */


