/* banner */

.banner-wrapper {
    position: relative;
    margin: 0 auto 50px;
    ;
    max-width: 1220px;
}

@media screen and (max-width :768px) {
    .banner-wrapper {
        position: relative;
        overflow: hidden;
        width: 100%;
    }
}

.swiper-container1 {
    width: 100%;
    height: 100%;
}

.swiper-container1 .swiper-slide {
    position: relative;
    text-align: center;
    font-size: 18px;
    background: #fff;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
    justify-content: center;
    -ms-flex-align: center;
    align-items: center;
}

.banner-wrapper img {
    width: 100%;
    height: 100%;
}

.b-title {
    position: absolute;
    left: 10%;
    top: 5%;
    font-family: sans-serif;
}

.b-title1 {
    top: 10%;
    left: calc(50% - 443px);
}

.b-title2 {
    left: 0;
    text-align: left;
}

.b-title2 h2 {
    padding-left: 40px;
}

.b-title h2 {
    font-size: 50px;
    font-weight: 600;
    color: #ea5404;
}

.b-title p {
    margin-top: 30px;
    padding-left: 40px;
    font-size: 30px;
    font-weight: 600;
    color: #0b4396;
}

@media screen and (max-width: 768px) {
    .b-title h2 {
        font-size: 20px;
    }
    .b-title p {
        margin-top: 10px;
        font-size: 12px;
    }
    .b-title1 {
        left: 0;
    }
    .b-title1 p {
        padding-left: 20px;
    }
    .b-title2 {
        left: 0;
    }
    .b-title2 p {
        margin-top: 20px;
    }
}

/* banner end */


