.hyzx-detail {
    margin-top: 12px;
    width: 70%;
    overflow: hidden;
    font-size: 14px;
    text-align: left;
}
.hyzx-article-detail {
    width: 100%;
    overflow: hidden;
    font-size: 14px;
    text-align: left;
}
.hyzx-article-detail h2 {
    padding: 24px 0;
    font-size: 28px;
    color: #333;
    line-height: 50px;
    font-weight: 600;
    text-align: center;
    background-color: #fff;
}
.time-author {
    padding: 0 50px;
    background-color: #fff;
}
.time-author span {
    font-size: 16px;
    color: #888;
    line-height: 40px;
}
.time-author span.fa-eye {
    margin-left: 30px;
}
.hyzx-article_id {
    padding: 50px;
    text-indent: 2em;
    line-height: 30px;
    font-size: 18px;
    background-color: #fff;
}
.hyzx-article_id strong {
    font-weight: 600;
}
.prev-next {
    margin: 12px 0;
    width: 100%;
    height: 50px;
    background-color: #fff;
}
.prev-next span {
    padding: 0 50px;
    font-size: 14px;
    width: 50%;
    line-height: 50px;
    cursor: pointer;
    white-space: nowrap;
    overflow: hidden;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
}
.prev-next span::before {
    font-size: 20px;
}
.prev-next span:hover {
    color: #ea5404;
}




.hyzx-category-wrapper {
    width: 100%;
    overflow: hidden;
    font-size: 14px;
    text-align: left;
}
.zx-article-item {
    position: relative;
    margin-bottom: 12px;
    padding: 16px;
    width: 100%;
    background-color: #fff;
    overflow: hidden;
    cursor: pointer;
    -webkit-box-shadow: 1px 2px 4px #ccc,
                2px 4px 6px #e5e8ec;
            box-shadow: 1px 2px 4px #ccc,
                2px 4px 6px #e5e8ec;
}
.zx-article-item:hover {
    top: -1px;
    left: 1px;
    -webkit-box-shadow: 1px 2px 4px #666,
                2px 4px 6px #ccc,
                3px 6px 8px #e5e8ec;
            box-shadow: 1px 2px 4px #666,
                2px 4px 6px #ccc,
                3px 6px 8px #e5e8ec;
}
.zx-article-item>img {
    width: 300px;
    height: 170px;
}
.zx-article-item>div {
    max-width: calc(100% - 300px);
    padding: 0 12px;
}
.zx-article-item>div>h4>span {
    width: calc(100% - 30px);
    font-size: 20px;
    font-weight: 600;
    line-height: 40px;
    white-space: nowrap;
    overflow: hidden;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
}
.zx-article-item>div>h4>img {
    margin-top: 10px;
}
.zx-article-item-content {
    position: relative;
    width: 100%;
    height: 100px;
    line-height: 25px;
    color: #888;
    overflow: hidden;
}
.zx-article-item-content::after {
    content: '...';
    position: absolute;
    right: .5em;
    bottom: 0;
}
.zx-article-item>div>div {
    margin-top: 14px;
}
.zx-article-item>div>div>span {
    padding-right: 20px;
    color: #666;
}
@media (max-width : 768px) {
    .hyzx-detail {
        width:  100%;
    }
    .zx-article-item>img {
        width: 100%;
    }
    .zx-article-item>div {
        max-width: 100%;
        width: 100%;
    }
    .zx-article-item-content::after {
        content: '...';
        position: absolute;
        right: 0;
        bottom: 0;
    }
    .hyzx-article-detail h2 {
        padding: 0;
        font-size: 18px;
    }
    .time-author,.hyzx-article_id,.prev-next span {
        padding: 0 12px;
    }
}


