/* zixun */

.zixun {
    margin-top: 8px;
    background-color: #fafafa;
}

.wenti {
    margin: 60px auto;
    max-width: 1220px;
}

.wenti .row {
    width: 100%;
}

.wenti h2 {
    padding-top: 20px;
    padding-bottom: 36px;
    text-align: center;
    font-size: 34px;
}

.ques {
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    position: relative;
    padding: 40px 20px 100px;
    height: 270px;
    border-left: 1px solid #ccc;
}

.ques h3 {
    margin-bottom: 20px;
    font-size: 18px;
    text-align: center;
}

.ques span {
    padding-bottom: 20px;
    display: block;
    width: 30%;
    margin: 0 auto;
    height: 0;
    border-top: 2px solid #0A66B3;
}

.ques p {
    text-indent: 2em;
    color: #999;
}

.angle {
    position: absolute;
    top: 0;
    left: 0;
    width: 0;
    height: 0;
    border: none;
    border: 23px solid transparent;
    border-top: 23px solid #2ba9df;
    border-left: 23px solid #2ba9df;
}

.angle span {
    position: absolute;
    top: -18px;
    left: -16px;
    color: #fff;
    font-size: 18px;
    font-style: italic;
}

/* zixun end */


