/* yewu */

.yewu {
    width: 100%;
}

.yw1,
.yw2,
.yw3 {
    padding: 50px 0;
}

.yewu-wrapper {
    margin: 0 auto;
    max-width: 1220px;
}

.yw1 {
    background-color: #f4f4f4;
}

.yw2 {
    background: -webkit-gradient(linear, left top, right bottom, from(#1446EE), color-stop(#04A3E5), to(#1446EE));
    background: -webkit-linear-gradient(left top, #1446EE, #04A3E5, #1446EE);
    background: -o-linear-gradient(left top, #1446EE, #04A3E5, #1446EE);
    background: linear-gradient(to right bottom, #1446EE, #04A3E5, #1446EE);
    /* background: #f4f4f4; */
}

.yw3 {
    background: #f4f4f4;
}

@media screen and (max-width: 768px) {
    .zhuce {
        margin: 20px 0;
    }
}

.zhuce .container {
    width: 100%;
}

.zhuce h3,
.zhuce p {
    margin: 0;
    white-space: nowrap;
}

.zhuce-title {
    height: 295px;
    padding: 0;
    background-color: #fff;
    overflow: hidden;
}

.jizhang-title {
    height: 220px;
    background-color: transparent;
}

@media screen and (max-width: 768px) {
    .jizhang-title {
        height: auto;
    }
    .zhuce-title {
        height: auto;
        padding-bottom: 40px;
    }
    .yw1,
    .yw3 {
        padding-top: 0;
        padding-bottom: 0;
    }
}

.zhuce-title h3 {
    margin: 20px 0 68px 20px;
    color: #666;
    font-size: 30px;
    font-family: sans-serif;
}

.jizhang-title h3 {
    margin: 20px 0 40px 20px;
    color: #fff;
}

.zhuce-title p {
    text-align: center;
    letter-spacing: .9px;
    height: 25px;
    line-height: 25px;
    font-size: 22px;
    font-family: sans-serif;
    color: #666;
}

.jizhang-title p {
    font-size: 22px;
    color: #fff;
}

.zhuce-dizhi,
.zhuce-fl {
    height: 290px;
    padding: 0;
    padding-top: 5px;
}

.zhuce-dizhi img,
.zhuce-fl img {
    -webkit-transition: .5s;
    -o-transition: .5s;
    transition: .5s;
}

.zhuce-dizhi>div:hover img,
.zhuce-fl>div:hover img {
    -webkit-transform: scale(1.2);
        -ms-transform: scale(1.2);
            transform: scale(1.2);
}

div.zhuce-dizhi>div:hover,
div.zhuce-fl>div:hover {
    position: relative;
    background-color: #f4f4f4;
    top: -3px;
}

@media screen and (max-width: 768px) {
    .jizhang-dt {
        height: 260px;
    }
    .jizhang-dt span a {
        margin-bottom: 20px;
    }
}

.zhuce-dizhi>div:first-child {
    margin: 0 0 5px 5px;
    width: calc(50% - 5px);
    height: 285px;
    background-color: #fff;
    overflow: hidden;
    -webkit-box-shadow: 2px 2px 2px 0 #dcdcdc;
            box-shadow: 2px 2px 2px 0 #dcdcdc;
}

.zhuce-dizhi a,
.zhuce-fl a {
    color: #666;
    text-decoration: none;
}

.jizhang-dt {
    padding: 10px;
}

.jizhang-dt span {
    display: block;
    width: 100%;
    height: 100%;
    border: 1px solid #000;
    border-radius: 8px;
    background-color: #fff;
    overflow: hidden;
}

.jizhang-dt span:hover {
    position: relative;
    top: -1px;
    background-color: #f4f4f4;
}

.jizhang-dt a {
    margin: 35px auto 12px;
    width: 155px;
    display: block;
    height: 40px;
    line-height: 40px;
    text-align: center;
    color: #b09e7a;
    letter-spacing: 1.14px;
    font-size: 22px;
    background: #fff;
    border: 1px solid #b09e7a;
    -webkit-box-shadow: 2px 2px 2px 0 #e2d6be;
    box-shadow: 2px 2px 2px 0 #e2d6be;
    border-radius: 3px;
    margin-bottom: 12px;
    -webkit-transition: .5s;
    -o-transition: .5s;
    transition: .5s;
}

.zhuce-dizhi>div:first-child a img {
    margin: 90px auto 20px;
    display: block;
    width: 40px;
    height: 40px;
}

.zhuce-dizhi a p,
.zhuce-fl a p,
.jizhang-dt p {
    text-align: center;
    font-size: 18px;
}

.jizhang-dt p {
    color: #666;
    font-size: 18px;
    line-height: 26px;
}

.zhuce-dizhi>div:nth-child(2),
.zhuce-dizhi>div:nth-child(3) {
    margin: 0 0 5px 5px;
    width: calc(50% - 5px);
    height: 140px;
    background-color: #fff;
    overflow: hidden;
    -webkit-box-shadow: 2px 2px 2px 0 #dcdcdc;
            box-shadow: 2px 2px 2px 0 #dcdcdc;
}

.zhuce-dizhi>div:nth-child(2) a img,
.zhuce-dizhi>div:nth-child(3) a img {
    margin: 30px auto 20px;
    display: block;
    width: 40px;
    height: 40px;
}

.zhuce-fl>div {
    margin: 0 0 5px 5px;
    width: calc(50% - 5px);
    height: 140px;
    background-color: #fff;
    overflow: hidden;
    -webkit-box-shadow: 2px 2px 2px 0 #dcdcdc;
            box-shadow: 2px 2px 2px 0 #dcdcdc;
}

.zhuce-fl>div a img {
    margin: 30px auto 20px;
    display: block;
    width: 40px;
    height: 40px;
}

