.pl-list li {
    padding: 30px 20px;
    font-family: sans-serif;
    border-bottom: 1px solid #ccc;
}

.pl-list li>img {
    margin-right: 20px;
    width: 40px;
    height: 40px;
    border-radius: 50%;
}

.pl-list b {
    margin: 0 10px;
}

.pl-list i {
    display: inline-block;
    width: 83px;
    height: 14px;
    background-image: url('../images/sprite_common_icon.png');
    background-repeat: no-repeat;
    background-position: 0 -200px;
}

.pl_content {
    margin-top: 30px;
    font-size: 16px;
}

.pl_img img {
    cursor: pointer;
}

.pl_lg_img img {
    max-width: 100%;
}

.pl_imgs {
    margin-top: 10px;
    display: inline-block;
}

.pl_imgs img {
    margin-left: 8px;
    max-width: 100px;
}


