/* fixed nav */

.fix-nav {
    position: fixed;
    top: 50%;
    right: 20px;
    -webkit-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    transform: translateY(-50%);
    z-index: 999;
}

@media screen and (max-width: 768px) {
    .fix-nav {
        top: 70%;
        right: 5px;
    }
}

.fix-nav ul li {
    display: block;
    margin-top: 2px;
    width: 46px;
    height: 46px;
    font-size: 22px;
    line-height: 46px;
    text-align: center;
    color: #fff;
    background-color: #0A66B3;
    cursor: pointer;
}

.erweima {
    position: absolute;
    top: 0;
    right: 46px;
    width: 0;
    height: 112px;
    overflow: hidden;
}

.erweima img {
    width: 112px;
    height: 112px;
}

/* fixed nav end */


