/* headpage */

.headpage {
    background-color: #323232;
}

.headpage-wrapper {
    margin: 0 auto;
    max-width: 1220px;
    line-height: 20px;
}

.headpage-wrapper ul {
    margin: 0;
}

.headpage-wrapper li.right {
    width: 80px;
}

.headpage i {
    line-height: 36px;
    font-size: 14px;
    color: #cfcfcf;
}

.headpage a {
    line-height: 36px;
    color: #ccc;
}

.headpage a:hover {
    color: #fff;
    text-decoration: none;
}

/* headpage end */

/* header-nav */

.header-wrapper {
    max-width: 1220px;
    margin: 0 auto;
}

.navbar {
    position: relative;
    border: none;
    margin-bottom: 0;
}

.nav-sm {
    position: absolute;
    display: none;
    top: 80px;
    right: 0;
    padding: 10px;
    width: 100px;
    background-color: rgba(0, 0, 0, .8);
    z-index: 99;
    text-align: center;
}

.nav-sm li {
    height: 38px;
}

.nav-sm a {
    display: block;
    line-height: 38px;
    color: #fcfcfc;
    font-size: 14px;
    letter-spacing: 1px;
}

.nav-sm a.actived {
    color: #fff;
}

.navbar-logo {
    position: relative;
    height: 65px;
    padding: 20px;
    padding-bottom: 0;
}

.nav-btn {
    display: none;
}

@media screen and (max-width: 768px) {
    .navbar-logo {
        height: 80px;
    }
    .navbar-logo img {
        height: 100%;
        padding-bottom: 20px;
    }
    .nav-wrapper {
        display: none;
    }
    .nav-btn {
        position: absolute;
        top: calc(50% - 11px);
        right: 15px;
        display: block;
        width: 22px;
        height: 22px;
        z-index: 99;
    }
    .nav-btn img {
        padding: 0;
        width: 100%;
    }
}

.navbar-logo img {
    height: 100%;
}

.nav-wrapper {
    margin: 0 auto;
    height: 50px;
    line-height: 50px;
    font-size: 0;
    background-color: #fff;
}

.nav-wrapper::-webkit-scrollbar {
    display: none;
}

.nav-wrapper ul {
    margin: 0 auto;
    width: 700px;
    font-size: 0;
    height: 50px;
}

.nav-wrapper ul li {
    height: 50px;
    width: 100px;
    padding: 0 6px;
    text-align: center;
    display: inline-block;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
}

.nav-wrapper ul li:last-child {
    margin-right: 0;
}

.nav-wrapper ul li a {
    display: block;
    line-height: 50px;
    font-size: 18px;
    font-family: sans-serif;
    color: #646464;
    text-decoration: none;
}

.nav-wrapper ul li:hover {
    color: #EA5302;
    border-bottom: 3px solid #EA5302;
}

.nav-wrapper ul li a.actived {
    color: #EA5302;
    font-weight: 600;
}

/* header end */

/* head-nav-fix */

div.head-nav-fix {
    padding: 12px 0;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: auto;
    background-color: #fff;
    z-index: 99999;
}

.head-nav-fix-wrapper {
    margin: 0 auto;
    max-width: 1220px;
}

.head-nav-fix-wrapper ul {
    width: 800px;
}

div.head-nav-fix img {
    float: left;
    height: 50px;
}

/* head-nav-fix end */


