/* log-reg */

.log-reg {
    position: fixed;
    top: 0;
    height: 0;
    z-index: 9999999;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, .65);
}

.log-reg-inner {
    position: relative;
    left: calc(50% - 250px);
    top: 15%;
    padding: 40px;
    width: 500px;
    height: auto;
    border-radius: 8px;
    background-color: #fff;
}
@media screen and (max-width : 768px){
    .log-reg-inner {
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        border-radius: 0;
    }
}
.sub-shut {
    position: absolute;
    top: 20px;
    right: 20px;
    width: 16px;
    height: 16px;
    background-image: url('../images/i_close.png');
    background-repeat: no-repeat;
    cursor: pointer;
    -webkit-transition: .3s;
    -o-transition: .3s;
    transition: .3s;
}
.sub-shut:hover {
    -webkit-transform: rotate(180deg);
    -ms-transform: rotate(180deg);
    transform: rotate(180deg);
}
.switch {
    line-height: 70px;
}

.switch p {
    font-size: 18px;
}

.switch span {
    text-decoration: none;
    color: #04A3E5;
    cursor: pointer;
}

.switch span:hover {
    color: #0E65F1;
}
.form-sub p {
    margin-bottom: 20px;
}
.form-sub p input {
    padding: 8px 12px;
    padding-left: 36px;
    line-height: 30px;
    font-size: 16px;
    width: 100%;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
}
.user-name {
    background-image: url('../images/BacktoPixel_0132.png');
    background-repeat: no-repeat;
    background-position: 12px center;
}
.msg-yzm {
    background-image: url('../images/BacktoPixel_0112.png');
    background-repeat: no-repeat;
    background-position: 12px center;
}
.user-phone {
    background-image: url('../images/BacktoPixel_0292.png');
    background-repeat: no-repeat;
    background-position: 12px center;
}
.user-pwd {
    background-image: url('../images/BacktoPixel_0122.png');
    background-repeat: no-repeat;
    background-position: 12px center;
}
.user-mail {
    background-image: url('../images/BacktoPixel_0022.png');
    background-repeat: no-repeat;
    background-position: 12px center;
}
.user-address {
    background-image: url('../images/BacktoPixel_0082.png');
    background-repeat: no-repeat;
    background-position: 12px center;
}
.log-tip {
    margin: 12px 0;
    color: crimson;
}
.forget {
    padding: 12px 4px;
}
.forget input {
    margin-right: 4px;
}
.forget label {
    color: #666;
    font-weight: 100;
    line-height: 44px;
}
.forget a {
    line-height: 44px;
    text-decoration: none;
}
.log-reg-sub {
    margin-top: 12px;
}
.log-reg-sub button {
    display: block;
    width: 100%;
    line-height: 50px;
    text-align: center;
    color: #fff;
    background-color: #04A3E5;
    -webkit-transition: .5s;
    -o-transition: .5s;
    transition: .5s;
    border: none;
}
.log-reg-sub button:hover {
    background: #0E65F1;
}
.form-sub2 .half-width input {
    width: 48%;
}
.get-yzm {
    width: 48%;
    line-height: 50px;
    text-align: center;
    color: #333;
    background-color: #dedede;
    cursor: pointer;
}
.get-yzm:hover {
    background-color: #ccc;
}
/* log-reg end */



