/* list */
.list {
    background-color: #fff;
}
.prog {
    margin: 0 auto;
    padding-bottom: 20px;
    max-width: 1220px;
}
.prog h2 {
    margin: 0;
    padding: 20px 0 10px 20px;
    border-bottom: 1px solid #ccc;
}
.itemList {
    margin: 0;
    margin-left: 40px;
    display: inline-block;
}
.itemList li {
    margin: 0 4px;
    padding: 6px 12px;
    display: inline-block;
    font-size: 18px;
    cursor: pointer;
    color: #fff;
    background: #ccc;
    border-radius: 6px;
}
li.active {
    background-color: #ea5404;
}
.itemList li:hover {
    background-color: #ea5404;
}
@media screen and (max-width : 768px){
    .prog h2 {
        padding: 0;
    }
    .prog h2 i {
        display: block;
        padding-top: 20px;
        padding-left: 20px;
    }
    .itemList {
        margin-left: 0;
        padding: 20px 0;
        width: 100%;
        display: -moz-felx;
        display: -ms-flex;
        display: -ms-flexbox;
        display: flex;
        -ms-flex-direction: row;
            flex-direction: row;
        -ms-flex-wrap: nowrap;
            flex-wrap: nowrap;
        -ms-flex-pack: distribute;
            justify-content: space-around;
    }
    .itemList li {
        padding: 0;
        width: 21%;
        height: 30px;
        line-height: 30px;
        font-size: 14px;
        text-align: center;
    }
}
.questions {
    padding: 20px 12px;
    display: none;
}
.questions ul {
    padding: 20px 60px;
    line-height: 30px;
    color: #333;
    background-color: #f5f5f5;
    border: 1px solid #ccc;
    border-radius: 4px;
}
.showme {
    display: block;
}
.questions h4 {
    display: inline-block;
    padding: 8px 20px;
    font-size: 18px;
    font-weight: bold;
}
/* list end */


