/* footer */

.footer {
    background-color: #333;
}

@media screen and (max-width: 768px) {
    .footer {
        margin-bottom: 90px;
    }
}

.foot-wrapper {
    margin: 0 auto;
    padding: 20px;
    max-width: 980px;
}

.footer img {
    margin-right: 40px;
}

.foot-wrapper div {
    padding-top: 20px;
}

.foot-wrapper p {
    font-size: 14px;
    line-height: 30px;
    color: #dedede;
}

/* footer end */


