.hyzx-wrapper {
    background-color: #f0f3f7;
    overflow: hidden;
    font-family: "Micrisoft YaHei", sans-serif;
    text-align: center;
}
.hyzx-inner {
    margin: 0 auto;
    max-width: 1220px;
    overflow: hidden;
}
.hyzx-nav {
    margin-top: 10px;
    width: 100%;
    font-size: 0;
    background-color: #fff;
    -webkit-box-shadow: 1px 2px 4px #ccc,
                2px 4px 6px #e5e8ec;
            box-shadow: 1px 2px 4px #ccc,
                2px 4px 6px #e5e8ec;
}
.zxfl {
    margin: 0 12px;
    display: inline-block;
    width: 100px;
    line-height: 60px;
    font-size: 16px;
    font-weight: 600;
    color: #444;
}
.zxfl-lists {
    display: inline-block;
    font-size: 16px;
    color: #444;
    line-height: 60px;
}
.zx-category {
    width: 100px;
    height: 60px;
    cursor: pointer;
}
li.actived {
    font-size: 16px;
    font-weight: 600;
    color: #ea5404;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    border-bottom: 2px solid #ea5404;
}
.zx-remen {
    margin-top: 12px;
    margin-left: 1%;
    width: 29%;
}
.hyzx-hm {
    position: relative;
    width: 100%;
    overflow: hidden;
    cursor: pointer;
}
.hyzx-hm img {
    width: 100%;
    -webkit-box-shadow: 1px 2px 4px #ccc,
                2px 4px 6px #e5e8ec;
            box-shadow: 1px 2px 4px #ccc,
                2px 4px 6px #e5e8ec;
}
.hyzx-hm span {
    position: absolute;
    left: 0;
    top: 50%;
    width: 100%;
    line-height: 30px;
    color: #333;
    font-size: 20px;
    font-weight: 600;
    text-align: center;
    z-index: 9;
}
.zx-hot {
    margin-top: 6px;
    padding: 3%;
    overflow: hidden;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    background-color: #fff;
    -webkit-box-shadow: 1px 2px 4px #ccc,
                2px 4px 6px #e5e8ec;
            box-shadow: 1px 2px 4px #ccc,
                2px 4px 6px #e5e8ec;
}
.zx-hot-lists {
    padding-bottom: 12px;
    overflow: hidden;
}
.zx-hot-lists li {
    cursor: pointer;
}
.zx-hot-first {
    margin: 12px 0;
}
.zx-hot-first img {
    width: 40%;
}
.zx-hot-first>div {
    padding: 0 8px;
    width: 60%;
    height: 80px;
}
.zx-hot-first>div .title {
    width: 100%;
    height: 30px;
    line-height: 30px;
    font-size: 20px;
    color: #000;
    font-weight: 600;
    white-space: nowrap;
    overflow: hidden;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
}
.zx-hot-first>div .title:hover {
    color: #ea5404;
}
.zx-hot-first>div .content {
    height: 50px;
    line-height: 25px;
    color: #999;
    font-size: 14px;
    overflow: hidden;
}
.zx-hot-item {
    font-size: 14px;
    line-height: 50px;
    border-bottom: 1px solid #dedede;
}
.zx-hot-item:last-child {
    border: none;
}
.zx-hot-item h4 {
    text-align: left;
    width: 190px;
    white-space: nowrap;
    overflow: hidden;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
}
.zx-hot-item span {
    color: #999;
}
@media (max-width : 768px) {
    .zxfl-lists {
        width: 100%;
    }
    .zx-category {
        width: calc( 100% / 3);
    }
    .zx-remen {
        display: none;
    }
}


