/* list */

.list {
    background-color: #fff;
}

.list-wrapper {
    margin: 0 auto;
    padding-top: 20px;
    max-width: 1220px;
    width: 1220px;
}

.listImg {
    width: 40%;
}

.listImg img {
    width: 100%;
}

.items {
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    padding-left: 60px;
    width: 60%;
    font-size: 0;
}

.items h3 {
    margin: 0;
    margin-bottom: 8px;
    font-size: 28px;
    line-height: 1.5em;
}

.items p {
    margin: 0;
    margin-bottom: 30px;
    padding: 0;
    line-height: 20px;
    font-size: 16px;
    color: #999;
    font-family: sans-serif;
}

.items .label {
    line-height: 20px;
    color: #666;
    font-size: 16px;
    font-family: sans-serif;
    font-weight: 100;
}

.item-lists li {
    margin-top: 12px;
    margin-right: 24px;
    display: inline-block;
    text-align: center;
    width: 165px;
    line-height: 40px;
    color: #D4D3D3;
    font-size: 16px;
    border: 1px solid #D4D3D3;
    border-radius: 4px;
    white-space: nowrap;
    overflow: hidden;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
    cursor: pointer;
}

li.chooseCol {
    color: #E95204;
    border-color: #E95204;
}

.item-lists li:hover {
    color: #E95204;
    border-color: #E95204;
}

.items>a {
    margin-top: 24px;
    display: block;
    font-size: 22px;
    color: #E95204;
    background-color: #F5F3EF;
    border: 1px solid #E95204;
    text-align: center;
    width: 174px;
    line-height: 50px;
    text-decoration: none;
    border-radius: 4px;
}

@media screen and (max-width: 768px) {
    .list-wrapper {
        width: 100%;
    }
    .listImg {
        width: 100%;
        display: block;
    }
    .items {
        padding: 0;
        width: 100%;
    }
    .items h3,
    .items p,
    .label {
        display: none;
    }
    .item-lists {
        margin: 0 auto;
        padding: 0 5px 15px;
        width: 90%;
        display: -ms-flexbox;
        display: flex;
        -ms-flex-direction: row;
            flex-direction: row;
        -ms-flex-wrap: wrap;
            flex-wrap: wrap;
        -ms-flex-pack: distribute;
            justify-content: space-around;
        border-radius: 8px;
        -webkit-box-shadow: 0 4px 18px 0 hsla(0, 0%, 69%, .34);
                box-shadow: 0 4px 18px 0 hsla(0, 0%, 69%, .34);
    }
    .item-lists li {
        margin: 15px 1px 0;
        width: 30%;
        height: 30px;
        line-height: 30px;
        font-size: 12px;
    }
    .item-lists~a {
        display: none;
    }
}

.prog {
    margin: 0 auto;
    padding-bottom: 20px;
    max-width: 1220px;
}

.prog h2 {
    margin: 0;
    padding: 20px 0 20px 20px;
    border-bottom: 1px solid #ccc;
}

.itemList {
    margin: 0;
    margin-left: 40px;
    display: inline-block;
}

.itemList li {
    padding: 8px 12px;
    display: inline-block;
    font-size: 18px;
    cursor: pointer;
    color: #fff;
    background: #ccc;
    border-radius: 6px;
}

li.active {
    background-color: #E95204;
}

.itemList li:hover {
    background-color: #E95204;
}

.zl-title {
    margin: 0;
    margin-top: 20px;
    padding-left: 20px;
    font-size: 22px;
    color: #646464;
    font-family: sans-serif;
}

.ziliaoList li,
.chengList li {
    padding-left: 20px;
    line-height: 2em;
}

ul.jiList {
    margin-top: 50px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
}

.jiList li {
    display: inline-block;
}

.chengList li {
    font-size: 18px;
    line-height: 2em;
    font-weight: 600;
    color: #333;
}

.jiList img {
    width: 100%;
}

/* list end */


